.theme-switcher-input {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 40px;
  height: 28px;
  cursor: pointer;
  margin: 0;
  display: block;
}

.theme-switcher-input:before {
  content: "";
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 24px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.theme-switcher-input:after {
  content: "";
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: left .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 1px;
  transform: translateY(-50%);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.theme-switcher-input.dark-theme:before {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.theme-switcher-input.dark-theme:after {
  left: 20.5px;
}

