// modal bach drop
.bacckdrop-sign {
  position: fixed;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background: radial-gradient(ellipse at top,
      #4F2EE8,
      #4764a8,
      #a28fd6,
      #b1b0df);
  background-size: 300% 300%;
  animation: gradient 15s ease infinite;
}


// WRAPER FOR MODAL FORM SIGN
.wrap_modal_sign {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: #FFFFFF;
  width: 579px;
  height: 516px;
  background: #F6F6F6;
  border: 2px solid #111111;
  border-radius: 18px;
}


// ELEMENTS FOWM
.form_sign {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
}

.form_sign input {
  width: 499px;
  height: 64px;

  padding-left: 40px;

  cursor: text;
  border-radius: 40px;
}

.input:not(:first-child) {
  margin-top: 28px;
}

.sign-btn-on-modal {
  width: 499px;
  height: 64px;

  font-size: 18px;
  line-height: 24px;

  letter-spacing: -0.01em;
  text-transform: uppercase;
}

.form_sign button {
  margin-top: 40px;
  cursor: pointer;
  border-radius: 40px;
  padding: 20px 173px;
  background: #111111;
  color: #F3F3F3;
}

.sign_list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.close-modal {
  cursor: pointer;

  width: 14px;
  height: 14px;

  position: absolute;
  right: 31px;
  top: 31px;

  display: flex;
  align-items: center;
  font-size: medium;
}





// icons
.form_sign i {
  position: absolute;
  right: 75;

  width: 24px;
  height: 18px;
}

.email-icon-modal-sign {
  top: 197;
}

.password-icon-modal-sign {
  top: 285;
}





// EFECTS FOR FORM
.close-modal:hover {
  animation: pulsing 1s infinite;
  color: #ff0000;
}

.form_sign button:hover {
  background: #EAC645;
  color: #111111;
}

.form_sign button:focus {
  background: #EAC645;
  color: #111111;
}

.input:hover {
  border-color: #EAC645;
}

.input:focus {
  border-color: #EAC645;
}

// animation
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@-webkit-keyframes pulsing {
  0% {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
  }

  50% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }

  100% {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
  }
}




// SCROLL UP
#scrollup {
  position: fixed;
  z-index: 99999;
  bottom: 60;
  right: 60;

  background-color: rgba(119, 174, 236, 0.767);
  border-radius: 50%;
  padding: 10px;

  font-size: 30px;
  color: #FFFFFF;

  transform: translate(50%, 50%);

  cursor: pointer;
}

#scrollup:hover {
  background: radial-gradient(ellipse at center,
      #7f86e6,
      #4751e7,
      #473aff);
  background-size: 300% 300%;
  animation: gradient 30s ease infinite;

  font-size: 60px;
  padding: 20px;
}

.isshowbtn {
  opacity: 1;
  transition: 0.7s;

  &__hide {
    opacity: 0;
    display: none;
  }
}

.signUpBtn {
  cursor: pointer;
  animation: pulsing 4s infinite;
}